import "./styles.css";
import { Canvas, useLoader } from "@react-three/fiber";
import { Suspense } from "react";
import { OrbitControls, PerspectiveCamera } from "@react-three/drei";
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'

import { Ground } from "./components/Ground";
// models
import BuildingModel from "../src/models/Building.gltf";

function Building() {
  const meshPosition = [5,0,0]
  const glb = useLoader(GLTFLoader, BuildingModel)
  return (
      <>
      <primitive object={glb.scene} scale={[.1, .1, .1]} position={meshPosition} />
      <spotLight color="#ADD8E6" intensity={1} position={[meshPosition[0] * 2,5, meshPosition[2]]} distance={10} />
      </>
  )
}

// Background settings
function BG() {
  return (
    <>
      <OrbitControls
        enableZoom={false}
        autoRotate={true}
        autoRotateSpeed={0.5}
        target={[0, .35, 0]}
        maxPolarAngles={1.45}
      />
      <PerspectiveCamera makeDefault fov={50} position={[3, 2, 5]} />

      <color args={[0, 0, 0]} attach="background" />

      <spotLight
        color={[1, 0.25, 0.7]}
        intensity={1.5}
        angle={0.6}
        penumbra={0.5}
        position={[5, 5, 0]}
        castShadow
        shadow-bias={-0.0001}
      />
      <spotLight
        color={[0.14, 0.5, 1]}
        intensity={2}
        angle={0.6}
        penumbra={0.5}
        position={[-5, 5, 0]}
        castShadow
        shadow-bias={-0.0001}
      />

      <Ground />
    </>
  );
}

function App() {
  return (
    <Suspense fallback={null}>
      <Canvas shadows>
        <BG />
      <Building/>
      </Canvas>
    </Suspense>
  );
}

export default App;
